<template>
    <v-container>
        <v-dialog v-model="deleteDialog" persistent max-width="290">
            <v-card>
                <v-card-title class="headline">?</v-card-title>
                <v-card-text>  .</v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click.native="deleteItem">Delete</v-btn>
                    <v-btn color="green darken-1" text @click.native="deleteDialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-row>
            <v-col cols="6" md="10">
                <v-text-field
                        label="ProjectName"
                        outlined
                        v-model="search"
                ></v-text-field>
            </v-col>
            <v-col cols="1" md="2">
                <v-btn block x-large
                       color="green lighten-2"
                       to="storming"
                >New Project
                </v-btn>
            </v-col>
        </v-row>

        <v-row>
            <v-col
                    v-for="(item, key) in filteredList"
                    :key="item.id"
                    cols="6"
                    md="4"
            >
                <v-card
                        outlined
                        class="mx-auto"
                        max-width="400"
                        max-height="400"
                >
                    <v-img
                            :src='"data:image/png;base64,"+item.img'
                            max-width="400"
                            max-height="300"

                    ></v-img>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <v-list-item-title class="headline mb-1">{{item.name}}</v-list-item-title>
                            <v-list-item-subtitle>Author : {{item.author}}</v-list-item-subtitle>
                            <v-list-item-subtitle>Modified Date : {{item.date}}</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <v-btn text :to="toUrl(item.name, item.author)">Edit</v-btn>

                        <v-btn
                                color="red"
                                text
                                @click="deleteConfirm(item.name, item.author)"
                        >
                            Delete
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
            <v-col
                    cols="6"
                    md="4"
            >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        align="center"
                >
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        name: "EventStormingListPage",
        data() {
            return {
                stormingList: [],
                addDialog: false,
                name: 'Test',
                deleteDialog: false,
                selectProjectName: '',
                selectAuthor: '',
                search: ''
            }
        },
        created() {
            var me = this;
            me.getList()
        },
        computed: {
            filteredList() {
                return this.stormingList.filter(eventstorming => {
                    return eventstorming.name.toLowerCase().includes(this.search.toLowerCase())
                })
            }
        },
        methods: {
            toUrl(projectName, author) {

                return "storming/" + author + "/" + projectName
            },
            deleteConfirm(projectName, author) {
                var me = this
                me.selectProjectName = projectName;
                me.selectAuthor = author;

                me.deleteDialog = true
            },
            deleteItem() {
                var me = this;

                me.$http.delete(`http://localhost:8081/delete?author=` + me.selectAuthor + "&projectName=" + me.selectProjectName).then(function () {
                    console.log("")
                    me.getList();

                    me.deleteDialog = false
                })
            },
            getList() {
                var me = this
                me.$http.get(`http://localhost:8081/getList`).then(function (result) {
                    // console.log(result.data)
                    console.log(" ")
                    me.stormingList = result.data
                })
            }
        }
    }
</script>

